import React from 'react'
import {
    Col
} from 'reactstrap';
import { Icon } from 'react-fa';

export default class MembershipCard extends React.Component {
    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            isOpen: true
        };
    }

    toggle() {
        this.setState({
            isOpen: !this.state.isOpen
        });
    }
    render() {
        const { data } = this.props;
        return (
            <Col md='4' className='membership' onClick={this.toggle}>
                <div className='thumbnail'>
                    <img alt={data.altImage} className='membership-image' height='178.0' src={data.image} width='178.0' />
                    <div className='caption'>
                        <h3>{data.title}</h3>
                        <p className='caption-text-bold'>{data.subTitle}</p>
                        <p>{data.startPrice}</p>
                        <div className='membership-description' hidden={this.state.isOpen}>
                            {
                                data.description.map((text, index) => {
                                    return (
                                        <p key={index}>{text}</p>
                                    )
                                }
                                )
                            }
                        </div>
                        <p className='buttons'>
                            <Icon name='angle-down' size='2x' className='angle-caption' hidden={!this.state.isOpen}/>
                            <Icon name='angle-up' size='2x' className='angle-caption' hidden={this.state.isOpen} />
                        </p>
                    </div>
                </div>
            </Col>
        )
    }
}


