import React from 'react'
import {
    Container,
    Row,
    Col,
    Jumbotron
} from 'reactstrap';
// import { LinkContainer } from 'react-router-bootstrap';
// import { Link } from 'react-router-dom';
// import { Icon } from 'react-fa';
import MembershipCard from './MembershipCard';
import CartItem from '../../api/membershipCard';
import CoworkingImag from '../../api/carouselCoworkingIname';
import CarouselSlider from '../CarouselSlider/CarouselSlider';
import './Coworking.css';

export default class Coworking extends React.Component {
    render() {
        return (
            <div>
                <Jumbotron className='coworking-jumbotron'>
                    <Container>
                        <Row>
                            <Col md='12' size='sm' className='col-sm-centered'>
                                <p className="tagline">join_the_crowd</p>
                                <h1>COWORKING</h1>
                            </Col>
                        </Row>
                    </Container>
                </Jumbotron>
                <Container>
                    <Row className='about-coworking'>
                        <Col md='8'>
                            <p>PARISOMA gives freelancers, entrepreneurs, remote workers and early-stage companies the space and tools they need to grow their businesses.</p>
                            <p>Membership includes much more than a desk (or ping-pong table) and a chair. Our members get access to over 100 classes and events a year that are specifically geared to help them with their latest endeavors as well as an engaged mentor community. But do we have blazing internet, printers/copiers, conference and meeting rooms, phone booths, bike racks, free Philz Coffee and premium tea? Does a one-legged duck swim in a circle? Free with any membership type.</p>
                            <p>Visiting or working remotely? We also offer Day Passes ($25/day) and a Virtual Office plan to let you enjoy PARISOMA on a flexible basis.</p>
                            <Row className='membership-plans'>
                                {
                                    CartItem.map((item, index) => {
                                        return (
                                            <MembershipCard data={item} key={index} />
                                        )
                                    })
                                }
                            </Row>
                        </Col>
                        <Col md={{ size: 3, offset: 1 }} className='take-a-tour col-sm-centered'></Col>
                    </Row>
                    <Row>
                        <Col md='12' xs='12' className='slider-coworking '>
                            <CarouselSlider item={CoworkingImag} />
                        </Col>
                    </Row>
                </Container>
            </div>
        )
    }
}


